@import "./variables";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  background-color: $black;
  color: $white;
  width: 100%;
  min-height: 100vh;

  scrollbar-width: 1rem;
  scrollbar-track-color: $grey;
  scrollbar-color: $black;
}

::-webkit-scrollbar {
  width: 1rem;
  background-color: $black;
}
::-webkit-scrollbar-button {
  background-color: $darkGrey;
}

::-webkit-scrollbar-thumb {
  background-color: $grey;
}

::-moz-selection {
  background: transparentize($white, 0.82);
}

::selection {
  background: transparentize($white, 0.82);
}

.pages-container {
  background-color: $black;
  color: $white;
}

.page-section {
  width: 100%;
  min-height: 100vh;
  max-height: max-content;
}

#home {
  min-height: 0;
  height: calc(100vh - 6rem);
}

#about {
  min-height: 90vh;
  height: max-content;
}

@import "./nav";
@import "./hero";
@import "./projects";
@import "./about";
@import "./footer";
