@import "./variables";
.about-container {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  min-height: 90vh;
  height: max-content;
  padding-top: 10rem;
  padding-bottom: 2rem;
  .about-left {
    padding: 2rem;
    img {
      width: 18rem;
      height: auto;
      border-radius: 0.6rem;
      opacity: 0.55;
    }
  }
  .about-right {
    display: flex;
    flex-direction: column;
    h1.about-heading {
      font-size: 5rem;
      font-family: $monoFont;
      font-weight: 500;
      color: $white;
      padding: 2rem;
    }
    p.about-content {
      max-width: 80rem;
      padding: 0 2rem 2rem 2rem;
      font-size: 2rem;
      font-family: $monoFont;
      font-weight: 400;
      color: transparentize($white, 0.12);
    }
  }
  .about-blob {
    transform: rotate(3.81deg) rotateZ(-180deg);
    position: absolute;
    bottom: 5%;
    right: 2%;
    opacity: 0.6;
  }
}

@media screen and (max-width: 945px) {
  .about-container {
    padding-top: 5rem;
    flex-direction: column;
    .about-left {
      padding: 2rem;
    }
    .about-right {
      p.about-content {
        font-size: 1.8rem;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .about-blob {
    width: 90%;
  }
}
