@import "./variables";
.projects-container {
  height: max-content;
  width: 100%;
  padding: 12rem 1rem 1rem 1rem;
  position: relative;
  z-index: 0;
  .projects-heading {
    width: 100%;
    font-size: 4rem;
    font-family: $monoFont;
    font-weight: 500;
    padding: 2rem;
  }
  .projects-list {
    padding: 2rem;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    a {
      text-decoration: none;
      color: $white;
      margin: 2rem;
    }
    .projects-list-item {
      height: 45rem;
      width: 30rem;
      box-shadow: 0px 5px 0px 6px rgba(99, 116, 133, 0.14);
      font-size: 1.6rem;
      padding: 2rem;
      border-radius: 1rem;
      cursor: pointer;
      transition: ease-in-out 120ms all;
      background-color: $black;
      .list-item-img {
        border-radius: 0.6rem;
        object-fit: cover;
        height: 75%;
        width: 100%;
      }
      & > div {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 25%;
        .list-item-name {
          font-size: 1.7rem;
          font-family: $monoFont;
          font-weight: 400;
          color: $white;
        }
        .list-item-desc {
          font-size: 1.5rem;
          font-family: $monoFont;
          font-weight: 400;
          color: $dimWhite;
        }
      }
      &:hover {
        box-shadow: 0px 15px 0px 6px rgba(99, 116, 133, 0.14);
        transform: translateY(-1rem);
      }
    }
  }
  .view-repos-btn {
    width: 100%;
    text-align: right;
    margin: 1rem 0;
    padding: 1rem;
    button {
      padding: 1rem;
      background-color: $darkGrey;
      border: none;
      outline: none;
      border-radius: 0.5rem;
      transition: 120ms ease-in-out background-color;
      cursor: pointer;

      a {
        text-decoration: none;
        font-family: $monoFont;
        font-weight: 500;
        font-size: 1.6rem;
        color: $white;
      }
      &:hover {
        background-color: $grey;
      }
    }
  }
}
.projects-blob {
  position: absolute;
  bottom: 18%;
  left: 1%;
  width: 25rem;
  height: auto;
  opacity: 0.7;
  z-index: -1;
}

@media screen and (max-width: 1366px) {
  .projects-container {
    .projects-list {
      display: grid;
      gap: 2.6rem;
      grid-template-columns: repeat(auto-fill, minmax(30rem, 1fr));
      a {
        margin: 0;
      }
      .projects-list-item {
        width: 100%;
        height: 45rem;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .projects-container {
    .projects-list {
      padding: 2rem 0rem;
    }
    .projects-heading {
      font-size: 2.5rem;
    }
    .view-repos-btn {
      button {
        background-color: $black;
        border: 1px solid darken($dimWhite, 36);
      }
    }
  }
  .projects-blob {
    width: 20rem;
    opacity: 0.4;
    left: 3%;
    bottom: 2%;
  }
}
