@import "./variables";

.footer-container {
  width: 100%;
  height: max-content;
  padding: 1rem;
  display: flex;
  border-top: 1px solid $grey;
  background-color: $black;
  p {
    font-size: 1.4rem;
    font-weight: 400;
    font-family: $sansFont;
  }
  p.copyright {
    color: $white;
  }
  span.cat {
    display: block;
    padding: 0.6rem 0;
    color: $dimWhite;
  }
}
