@import "./variables";

.nav-bar {
  position: fixed;
  top: 0;

  width: 100vw;
  height: max-content;
  background-color: transparentize($black, 0.12);
  z-index: 9999;
  @supports ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
    background-color: transparentize($black, 0.2);
    backdrop-filter: blur(4px);
  }
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  .links {
    width: 100%;
    display: flex;
    flex-direction: row;

    li {
      padding: 1.4rem;
      margin: 1rem 3rem;
      list-style: none;
      font-family: $monoFont;
      font-weight: 500;
      font-size: 1.5rem;
      color: $white;
      cursor: pointer;
      transition: all ease-in-out 200ms;
      position: relative;
      user-select: none;
    }

    li.active::before {
      content: "";
      position: absolute;
      top: 80%;
      right: 0;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 2.4rem;
      height: 0.3rem;
      background-color: $accent;
      transition: all ease-in-out 150ms;
    }

    li.active {
      color: $accent;
      font-weight: 600;
    }
  }
}

@media screen and (max-width: 945px) {
  .nav-bar {
    .links {
      justify-content: center;
      li {
        font-size: 1.6rem;
        background-color: lighten($black, $amount: 3);
        border-radius: 0.6rem;
      }
      li.active::before {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .nav-bar {
    .links {
      li {
        margin: 0.6rem;
      }
    }
  }
}

