@import "./variables";

@keyframes float-b {
  0% {
    top: 50%;
    transform: translateY(-50%);
  }
  50% {
    transform: translateY(calc(-50% - 4px));
  }
  100% {
    transform: translateY(-50%);
  }
}

@keyframes float-b-M {
  0% {
    top: 40%;
    transform: translateY(-50%);
  }
  50% {
    transform: translateY(calc(-50% - 4px));
  }
  100% {
    transform: translateY(-50%);
  }
}

@keyframes float-s {
  0% {
    top: 50%;
    transform: translateY(-100%);
  }
  50% {
    transform: translateY(calc(-100% - 13px));
  }
  100% {
    transform: translateY(-100%);
  }
}

@keyframes float-s-M {
  0% {
    top: 45%;
    transform: translateY(-100%);
  }
  50% {
    transform: translateY(calc(-100% - 13px));
  }
  100% {
    transform: translateY(-100%);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.hero-container {
  position: relative;
  top: 6rem;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: flex-start;
  transition: all ease-in 2s;
  animation: fade-in 1.5s forwards;
  display: flex;

  .hero-text {
    padding: 4rem 15rem;
    position: relative;
    z-index: 1;

    h1 {
      font-family: $monoFont;
      font-size: 4.5rem;
      font-weight: 500;
      color: $white;
      .hero-ln-break-one,
      .hero-ln-break-two {
        display: none;
      }
    }
    h1.dim {
      color: $dimWhite;
    }
  }
  .hero-img {
    img {
      transition: all ease-in-out 200ms;
    }
    img.blob-big {
      height: auto;
      position: absolute;
      top: 50%;
      right: 10%;
      transform: translateY(-50%);
      animation: infinite float-b 3s;
      animation-delay: 1s;
    }
    img.blob-small {
      position: absolute;
      top: 50%;
      right: 5%;
      transform: translateY(-100%);
      animation: infinite float-s 2s;
      animation-delay: 1s;
    }

    img.grid-dark {
      position: absolute;
      top: 8rem;
      right: 2%;
      width: 20rem;
    }
    img.grid-light {
      position: absolute;
      bottom: 6%;
      left: 4%;
    }
  }
}

@media screen and (max-width: 945px) {
  .hero-container {
    .hero-text {
      h1.dim {
        color: $white;
      }
    }
    .hero-img {
      img.blob-big {
        width: 50rem;
        height: auto;
        opacity: 0.45;
      }
      img.blob-small {
        width: 30rem;
        height: auto;
        opacity: 0.6;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .hero-container {
    align-items: flex-end;
    .hero-text {
      padding: 5rem 0 6rem 5rem;
      h1 {
        font-size: 3.8rem;
        .hero-ln-break-two {
          display: initial;
        }
      }
    }
    .hero-img {
      img.blob-big {
        width: 90%;
        top: 40%;
        right: 4%;
        animation-name: float-b-M;
      }
      img.blob-small {
        top: 45%;
        right: 3%;
        width: 25rem;
        animation-name: float-s-M;
      }
      img.grid-dark {
        width: 30rem;
        opacity: 0.65;
      }
      img.grid-light {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 440px) {
  .hero-container {
    .hero-text {
      h1 {
        .hero-ln-break-one {
          display: initial;
        }
      }
    }
    .hero-img {
      img.grid-dark {
        display: none;
      }
    }
  }
}
